import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      name: 'Index',
      path: '/',
      component: () => import('@/pages/Index/template.vue')
    },
    {
      name: 'Detail',
      path: '/detail/:blogId',
      component: () => import('@/pages/Detail/template.vue')
    },
    {
      name: 'Tag',
      path: '/tag/:tagName',
      component: () => import('@/pages/Tag/template.vue')
    },
    {
      name: 'Category',
      path: '/category/:categoryName',
      component: () => import('@/pages/Category/template.vue')
    },
    {
      name: 'Guide',
      path: '/guide',
      component: () => import('@/pages/Guide/template.vue')
    },
    {
      name: 'Unzip',
      path: '/unzip',
      component: () => import('@/pages/Unzip/template.vue')
    },
    {
      name: 'Message',
      path: '/message',
      component: () => import('@/pages/Message/template.vue')
    },
    {
      name: 'Register',
      path: '/register',
      component: () => import('@/pages/Register/template.vue')
    },
    {
      name: 'Login',
      path: '/login',
      component: () => import('@/pages/Login/template.vue')
    },
    {
      name: 'Search',
      path: '/search/:keyword',
      component: () => import('@/pages/Search/template.vue')
    }
  ],
  mode: 'history',
  base: '/'
})

export default router

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0)
})



